
.my-color {
    color: red;
}

.my-font {
    font-family: 'Work Sans';
}

.login-pf body {
    background: linear-gradient(0, #3F3F46, #6A717F);
}

span {
    color: #dcdcdc
}

#kc-login {
    background: rgba(199, 104, 46, 0.87);
}

#kc-login:hover {
    background-color: #C7682E;
}

