.btn.disabled, .btn[disabled], fieldset[disabled] .btn {
    background-color: #fa8c52 !important;
}

img {
    height: 100%;
    width: 100%;
}

@media (min-width: 0px) {
    img {
        height: 100%;
        width: 100%;
        margin:auto;
        display:block;
    }
}

@media (min-width: 1000px) {
    img {
        height: 80%;
        width: 80%;
        margin:auto;
        display:block;
    }
}
